@import 'theme';

#page-wrap {
  max-height: 95vh;
  overflow-y: scroll;
}

.todos--div-container {
  margin: auto;
  width: 60%;
  @media (max-width: 500px) {
    width: 90%;
  }
  :hover {
    background: $light-red;
  }
}

.Todo--div-container {
  width: 100%;
  padding: 5px;
  display: inline-flex;
  justify-content: space-between;
}

.todos--div-addTodo {
  margin: 0px auto 0px auto;
  padding: 20px 5px 5px 5px;
  width: 60%;
  text-align: center;
  @media (max-width: 500px) {
    width: 100%;
    padding: 10px;
  }
}

.todos--add-input {
  width: 50%;
  border: 1px solid $secondary-color;
}

.todos--add-button {
  background: $light-color;
}

.todo--span-name {
  margin-left: 5px;
  width: 80%;
  overflow-wrap: break-word;
  font-size: 20px;
}

.completed {
  text-decoration: line-through;
}

.deleteTodo-button {
  border: none;
  background: inherit;
}

.todo--button-container {
  display: flex;
  flex-direction: row;
}
